import { Component } from '@angular/core';
import { NavController, IonicPage  ,NavParams,ActionSheetController,AlertController,LoadingController} from 'ionic-angular';
import {Observable} from 'rxjs/Rx';
import { SecurityProvider } from '../../providers/security/security';
import { Camera, CameraOptions } from '@ionic-native/camera';
import { ModalController } from 'ionic-angular';
import { TranslateService } from "@ngx-translate/core";
import { Events } from 'ionic-angular';


//import { TabsPage } from '../tabs/tabs';
@IonicPage()
@Component({
  selector: 'page-my-recipes',
  templateUrl: 'my-recipes.html',
})
export class MyRecipesPage {
Category;
Category2
navlen
cat;
list;
pop_div;
mycategory_data;
count=0;
savelength
post;
savedetail
fav_length
fav_detail
camera_post
Recipes
pic
urltext
addurlvalue:boolean;
RecipeLink
Menu
eventData
menu_data
eventType
popupMessages
currentLang
  constructor(public loadingCtrl: LoadingController,public alertCtrl: AlertController,public modalCtrl: ModalController,private camera: Camera,public actionSheetCtrl:ActionSheetController, public securityProvider:SecurityProvider,public navCtrl: NavController, public navParams: NavParams,private translate: TranslateService, public events: Events ) {
	   this.currentLang=this.translate.currentLang;
	  events.subscribe('language:updated', _badgeValue => {
				  this.currentLang = _badgeValue;
				 	//alert(this.translate.currentLang);
			  
			}); 
	  	//this.navCtrl.setRoot(TabsPage);
	 // alert(this.navCtrl.length())
	

	  	   	 this.translate.get('popup').subscribe(
  value => {
    // value is our translated string
	this.popupMessages=value;
  }
)
    this.list='false'
     this.pop_div='false';
     this.post=this.navParams.get('post');
     this.camera_post=this.navParams.get('camera_post');
     this.Recipes={}
     this.addurlvalue=false;
     this.RecipeLink=this.navParams.get('RecipeLink');
	// alert(JSON.stringify(this.RecipeLink));
     this.Menu=this.navParams.get('Menu');
     this.eventType=this.navParams.get('eventType');
     this.eventData=this.navParams.get('eventData')
     this.menu_data=this.navParams.get('menu_data');
  }

  ionViewDidLoad() {
    console.log('ionViewDidLoad MyRecipesPage');
	
  }
  onNewRecipe(){
     this.pop_div='false';
    this.navCtrl.push('NewRecipesPage');
  }
onIconlist(){
    this.list='false'
}
onIcongrid(){
     this.list='true'
}
onFridgeSearch(){
  this.navCtrl.push('FridgesearchPage');
}

presentPopover(){
  this.count++;
  if(this.count%2!=0){
   this.pop_div='true'; 
  }
   else{
     this.pop_div='false'; 
   }
  }

    getItems(ev: any) {
        let val = ev.target.value;
        if (val && val.trim() != '') {
            this.Category2 = this.Category.filter((p) => {
                return (p.category.toLowerCase().indexOf(val.toLowerCase()) > -1);
            })
        } else {
             this.Category2  = this.cat;
             console.log(JSON.stringify(this.Category2));
        }
    }

       ngOnInit(){
        this.getsaveRecipes()
        this.getFavourite()
        var a=localStorage['user_id']
           this.securityProvider.MyRecipe(a)
         .subscribe(data  =>{ 
           console.log("cat_data"+JSON.stringify(data));
           this.Category=data.recipes;
           for (var i = 0; i < this.Category.length; i++) {
             this.Category[i].order=this.Category[i].categoryData.length;
           };
            this.Category2=this.getOrderData(this.Category);
            this.cat=this.getOrderData(this.Category);
         console.log("cat_data"+JSON.stringify(this.Category2));
             setTimeout(() => {
          this.ngOnInit();
        }, 10000);
        }),
        
        error  => {}
		
		this.navlen=this.navCtrl.length();

    }
    catImage(data){
      var img="assets/imgs/catRecipe.png";
      if(data==[] || data=='' || data==""){
      
         return img;
      }
       else{
              for(let a of data){
          let b=a.mainImage;
          if(b!=''){
            return  b;
          }
          else{
            return img;
          }
          
        }

          }
          
    }
	 /*catImage(data, categoryImage){
         var img="assets/imgs/catRecipe.png";
		 if(!categoryImage){
     if(data==[] || data=='' || data==""){
      
         return img;
      }
       else{
              for(let a of data){
          let b=a.mainImage;
          if(b!=''){
            return  b;
          }
          else{
            return img;
          }
          
        }

          }
		 }else{
			 return categoryImage;
		 }
          
    } */
    getsaveRecipes(){
       var a=localStorage['user_id']
           this.securityProvider.MyRecipes(a)
         .subscribe(data  =>{ 
		// alert(JSON.stringify(data));
           console.log("cat_data"+JSON.stringify(data));
           this.savedetail=data.a;
           this.savelength=data.a.length;
		   this.savedetail=this.savedetail.filter(d=>d.recipe_detail!=null);
      
        }),
        error  => {}
    }
    getFavourite(){
       var a=localStorage['user_id']
           this.securityProvider.FavRecipes(a)
         .subscribe(data  =>{ 
           console.log("fav_data"+JSON.stringify(data));
        this.fav_length=data.favourites.length;
        this.fav_detail=data.favourites;
        }),
        error  => {}
    }
    onRecipes(a){
		//localStorage=""
		//alert(this.post);
		    this.pop_div='false';
		if(this.post){
		let myModal = this.modalCtrl.create('MyRecipeInsidePage',{ eventData:this.eventData,menu_data:this.menu_data,eventType:this.eventType,recipes_data:a,list:this.list,post:this.post,camera_post:this.camera_post,RecipeLink:this.RecipeLink,Menu:this.Menu});
		myModal.present();
		}else{
  
      this.navCtrl.push('MyRecipeInsidePage',{ eventData:this.eventData,menu_data:this.menu_data,eventType:this.eventType,recipes_data:a,list:this.list,post:this.post,camera_post:this.camera_post,RecipeLink:this.RecipeLink,Menu:this.Menu});
		}
	}
getOrderData(array){
 array.sort(function(a,b){return b.order - a.order})
 return array
}

OnSavedRecipes(){
this.navCtrl.push('SaverecipelistPage',{savedetail:this.savedetail,list:this.list,post:this.post});
}
OnFavouriteREcipe(){
  this.navCtrl.push('FavoritelistPage',{savedetail:this.fav_detail,list:this.list,post:this.post});
}

onImports(){
  let actionSheet = this.actionSheetCtrl.create({
      title: this.popupMessages.Select_Image_Source,
      buttons: [{ 
          text: this.popupMessages.Load_from_Library,
          handler: () => {
            this.fromgallery();
            this.pop_div=false;
          }
        },
        {
          text: this.popupMessages.use_camera,
          handler: () => {
            this.fromcamera();
             this.pop_div=false;
          }
        },
        {
          text: this.popupMessages.cancel,
          role: 'cancel'
        }
      ]
    });
    actionSheet.present();
}

fromcamera1(){
this.pic=''
	var img=this.pic;
	 this.pic = 'data:image/jpeg;base64,' + this.pic;
	 let modal = this.modalCtrl.create('ImportRecipeImagePage',{pic:this.pic,mainImage:img});
           modal.present();
}
fromcamera(){
 this.camera.getPicture({
      destinationType: this.camera.DestinationType.DATA_URL,
      sourceType: this.camera.PictureSourceType.CAMERA,
      encodingType: this.camera.EncodingType.JPEG,
      saveToPhotoAlbum: false,
      allowEdit:false,
	  correctOrientation:false
    }).then((imageData) => {
      let base64Image = imageData;
      this.pic = 'data:image/jpeg;base64,' + imageData;
      this.Recipes.mainImage = base64Image;
          let modal = this.modalCtrl.create('ImportRecipeImagePage',{pic:this.pic,mainImage:imageData});
           modal.present();
      console.log(this.Recipes.mainImage);
    }, (err) => {
      console.log('camera not working');
    })
}

fromgallery(){
    this.camera.getPicture({
      destinationType: this.camera.DestinationType.DATA_URL,
      sourceType: this.camera.PictureSourceType.PHOTOLIBRARY,
      encodingType: this.camera.EncodingType.JPEG,
      saveToPhotoAlbum: false,
      allowEdit:false
    }).then((imageData) => {
      let base64Image = imageData;
      this.pic = 'data:image/jpeg;base64,' + imageData;
      this.Recipes.mainImage = base64Image;
       let modal = this.modalCtrl.create('ImportRecipeImagePage',{pic:this.pic,mainImage:imageData});
    modal.present();
      console.log(this.Recipes.mainImage);
    }, (err) => {
      console.log('gallery not working');
    })
}

AddUrl(){
  this.pop_div='false';
  this.addurlvalue=true;
}

onRecipeTextAdd(){
      var a = {
      url: this.urltext
    }
    let loading = this.loadingCtrl.create({
        content: this.popupMessages.Please_wait
      });
    Observable.fromPromise(loading.present())
      .flatMap(data => this.securityProvider.AddUrl(a))
      .subscribe(data =>
        loading.dismiss().then(() => {
          console.log("data"+JSON.stringify(data));
          this.addurlvalue=false;
          var a=data;
          var b=a
    
            if (b) {
             this.urltext='';
            this.navCtrl.push('ImportRecipeUrlPage',{ImportUrlData:b});
            }
        }),
        error =>
        loading.dismiss().then(() => {
          console.log("err"+JSON.stringify(error));
          this.addurlvalue=false;
        })
      );
}
goback() {
   this.navCtrl.pop();
   console.log('Click on button Test Console Log');
}
onCancel(){
   this.addurlvalue=false;
}
}



